import Sidebar from './Sidebar';
import Footer from './Footer';
import axios from 'axios';

const useStyles = theme => ({
  mainGrid: {
    marginTop: theme.spacing(3),
  },
});

const sections = [
  { title: 'Technology', url: '/tech' },
  { title: 'Design', url: '#' },
  { title: 'Culture', url: '#' },
  { title: 'Business', url: '#' },
  { title: 'Politics', url: '#' },

];

